* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* ================================== GLOBAL CSS =================================================== */

.container {
  width: 80%;
  margin: 0 auto;
  height: 100%;
  position: relative;
}

.app-container {
  width: 80%;
  margin: 0 auto;
  height: 100%;
  position: relative;
}

@media (max-width: 500px) {
  .container {
    width: 95%;
  }
}
#nav {
  width: 100%;
  height: 60px;
  background: #3c4245;
  color: #eee;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.599); }
  #nav .nav-inner {
    height: 100%;
    display: flex;
    justify-content: space-between; }
    #nav .nav-inner .nav-logo {
      line-height: 60px;
      font-weight: 600;
      font-size: 23px;
      text-decoration: underline; }
    #nav .nav-inner .user-image-container {
      position: relative;
      height: 60px;
      width: 60px; }
      #nav .nav-inner .user-image-container .image {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        position: absolute;
        top: 10px;
        right: 0px;
        border: 2px solid #ffffff85;
        cursor: pointer; }
      #nav .nav-inner .user-image-container .user-data {
        position: absolute;
        bottom: -83px;
        box-shadow: 0px 0px 5px 0px black;
        right: 0;
        z-index: 2;
        border: 1px solid #eee;
        background: #3c4245;
        padding: 15px 0px;
        border-radius: 4px; }
        #nav .nav-inner .user-image-container .user-data::before {
          position: absolute;
          content: "";
          top: -32px;
          right: 7px;
          border-bottom: 21px solid white;
          border-top: 11px solid transparent;
          border-right: 12px solid transparent;
          border-left: 12px solid transparent; }
        #nav .nav-inner .user-image-container .user-data::after {
          position: absolute;
          content: "";
          top: -20px;
          right: 9px;
          border-bottom: 20px solid #3c4245;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent; }
        #nav .nav-inner .user-image-container .user-data .sign-out {
          width: 130px;
          color: #ffffffd6;
          padding: 9px;
          border-top: 1px solid #eee;
          border-bottom: 1px solid #eee;
          background: #3c4245;
          color: #ffffffd6;
          outline: none;
          -webkit-transition: background 400ms ease, color 400ms ease;
          transition: background 400ms ease, color 400ms ease;
          cursor: pointer; }
          #nav .nav-inner .user-image-container .user-data .sign-out:hover {
            border: none;
            background: #ffffffd6;
            padding: 10px;
            color: #3c4245; }
    #nav .nav-inner .btn {
      position: absolute;
      top: 12px;
      right: 0;
      border: none;
      padding: 10px 20px;
      border-radius: 20px;
      color: #3c4245;
      outline: none;
      cursor: pointer; }

.spinner {
  width: 15px;
  height: 15px;
  background: transparent;
  margin-right: 7px;
  border-radius: 50%;
  border-top: 2px solid #ffecec;
  -webkit-animation: spin infinite;
          animation: spin infinite;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; }

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

#add-contact {
  padding-right: 20px !important; }

.contact-header {
  padding-bottom: 20px;
  font-size: 26px;
  text-align: left;
  -webkit-text-decoration: dotted;
          text-decoration: dotted;
  font-weight: 600; }

.form-group {
  position: relative;
  margin-bottom: 20px; }
  .form-group label {
    position: absolute;
    font-size: 30px;
    color: red;
