  outline: none;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.6); }

.modal-title {
  text-align: center;
  padding: 10px 0px;
  font-size: 20px;
  color: #eee;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee; }

.modal-actions {
  display: flex;
  justify-content: space-between;
