.modal-btn-actions {
  display: flex;
  justify-content: center;
  border: none;
  padding: 10px 29px;
  border-radius: 20px;
  color: #3c4245;
  outline: none;
  cursor: pointer;
  align-items: center;
  width: 48%; }
  .modal-btn-actions:last-child {
    background: #3c4245;
    color: #eee;
    border: 1px solid #eeee; }

label {
